import React from 'react'
import styled from 'styled-components'
import {
  isNil,
  length,
  keys,
  pathOr,
  gt,
  __,
  map,
  filter,
  always,
  ifElse,
  join,
} from 'ramda'
import cx from 'classnames'
import cheerio from 'cheerio'
import { Row, Col, Divider as AntDivider, Form } from 'antd'
import { useReportJson } from '@/services/report'
import { useBreadcrumb } from '@/hooks/useApp'
import { useAuth } from '@/hooks/useAuth'
import { createFormItemLayout } from '@/utils/form'
import {
  Snippets,
  SnippetProps,
  MedicalOrder,
  ReportTemplateSettingOption,
} from '@/constants/types'
import { snippetTypes } from '@/components/snippet-template-list'
import { Input } from '@/components/input'
import { mapIndexed, toThousandSeparator, hasPath } from '@/utils/webHelper'
import { Divisions } from '@/constants/enums'
const { compose } = require('ramda')

type Props = {
  className?: string
  style?: React.CSSProperties
  data: {
    template: any
    snippetList: Snippets
    medical: MedicalOrder | null
  }
  exporting?: boolean
}

const formItemLayout = createFormItemLayout(8)
const shortFormItemLayout = createFormItemLayout(3)

const FormItem = styled(Form.Item)`
  margin-bottom: 12px;
`

const Divider = styled(AntDivider)`
  margin: 16px 0;
`

const SnippetPreviewWrapper = styled.div`
  .ant-card-head {
    min-height: auto;
  }

  .ant-card-head-title {
    padding-top: 0;
    font-size: 14px;
  }

  .ant-card-body {
    padding-top: 6px;
    padding-bottom: 8px;
    padding-left: 18px;
  }
`

export default function ReportTypingPreview({ className, style, data }: Props) {
  useBreadcrumb([{ name: '輸入報告' }, { name: '文件報告' }])

  const { json } = useReportJson({ orreqno: data.template.orreqno })

  const { snippetList, medical } = data

  const { user } = useAuth()

  const getHtmlText = (
    { html = '', valuesJson = [] }: SnippetProps,
    idx?: number
  ) => {
    const $ = cheerio.load(html)

    const getValue = (idx: number, val: any) => {
      // PDF 不必替換成編碼
      return pathOr(toThousandSeparator(val), [idx, 'codexxx'], valuesJson)
    }

    $('.template-cloze__control').each((idx, el) => {
      const $el = $(el)
      $el.replaceWith(
        (el as any).name === 'select'
          ? ' '
          : '' + getValue(idx, $el.val()) + ' '
      )
    })

    const div = document.createElement('div')
    div.innerHTML = $.html()

    const text = div.textContent || div.innerText || ''
    div.remove()

    return `${!idx ? '' : idx + '. '}${text}`
  }

  const _snippetList = pathOr(snippetList, ['setting'], json)

  const renderReport = ({ useCode = false }: any = {}) => {
    let idx = 1
    return (
      <>
        <Row className="mb-3">
          <Col span={24}>
            <div className="text-xl text-center mt-2">
              高雄榮總
              <span className="mx-6">
                {user.division === Divisions.RAD ? '放射線部' : '病理檢驗部'}
              </span>
              檢查報告單
            </div>
          </Col>
        </Row>
        <Divider />
        <Row gutter={16}>
          <Col span={8}>
            <FormItem {...formItemLayout} label="病房床號">
              <Input readOnly bordered={false} value={medical?.hbed} />
            </FormItem>
          </Col>
          <Col span={8}>
            <FormItem {...formItemLayout} label="病患姓名">
              <Input readOnly bordered={false} value={medical?.hnamec} />
            </FormItem>
          </Col>
          <Col span={8}>
            <FormItem {...formItemLayout} label="病歷號">
              <Input readOnly bordered={false} value={medical?.hhisnum} />
            </FormItem>
          </Col>
        </Row>
        <Row gutter={16}>
          <Col span={8}>
            <FormItem {...formItemLayout} label="病患性別">
              <Input readOnly bordered={false} value={medical?.hsex} />
            </FormItem>
          </Col>
          <Col span={8}>
            <FormItem {...formItemLayout} label="病患身分">
              <Input readOnly bordered={false} value={medical?.cfinancl} />
            </FormItem>
          </Col>
          <Col span={8}>
            <FormItem {...formItemLayout} label="出生日期">
              <Input
                readOnly
                bordered={false}
                value={medical?.hbirthdt || medical?.hbirthd2}
              />
            </FormItem>
          </Col>
        </Row>
        <Row gutter={16}>
          <Col span={8}>
            <FormItem {...formItemLayout} label="申請序號">
              <Input readOnly bordered={false} value={medical?.orreqno} />
            </FormItem>
          </Col>
          <Col span={8}>
            <FormItem {...shortFormItemLayout} label="開立科別">
              <Input readOnly bordered={false} value={medical?.csect} />
            </FormItem>
          </Col>
          <Col span={8}>
            {user.division === Divisions.RAD ? (
              <FormItem {...formItemLayout} label="檢查日期">
                <Input readOnly bordered={false} value={'xxxx'} />
              </FormItem>
            ) : (
              <FormItem {...formItemLayout} label="病理號">
                <Input readOnly bordered={false} value={medical?.orworkno} />
              </FormItem>
            )}
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <FormItem {...shortFormItemLayout} label="醫囑項目">
              <div
                dangerouslySetInnerHTML={{ __html: `${medical?.orword}` }}
                style={{
                  maxWidth: '100%',
                  whiteSpace: 'initial',
                  borderBottom: '1px solid #d9d9d9',
                  paddingBottom: 5,
                  paddingLeft: 5,
                  marginLeft: -15,
                }}
              />
            </FormItem>
          </Col>
        </Row>
        {user.division === Divisions.RAD && (
          <Row>
            <Col span={24}>
              <FormItem {...shortFormItemLayout} label="檢查日期">
                <Input readOnly bordered={false} value={'xxxx'} />
              </FormItem>
            </Col>
          </Row>
        )}
        <Divider />
        <Row>
          <Col span={24}>
            <SnippetPreviewWrapper>
              {compose(
                map((key: string) => (
                  <div
                    key={key}
                    css={{
                      fontSize: 15,
                      lineHeight: 1.65,
                      color: 'rgba(0, 0, 0, 0.85)',
                    }}>
                    {compose(
                      gt(__, 0),
                      length,
                      pathOr([], [key])
                    )(snippetList) && (
                      <h2
                        className="my-1 font-normal underline"
                        css={{ fontSize: 16 }}>
                        {key}
                      </h2>
                    )}
                    {compose(
                      mapIndexed(({ setting, ...x }: SnippetProps) => (
                        <div key={x.id}>
                          {(() => {
                            let className = `ml-0`

                            if (x.inline) {
                              return (
                                <div
                                  className={className}
                                  dangerouslySetInnerHTML={{
                                    __html: getHtmlText(x as any, idx++),
                                  }}
                                />
                              )
                            }

                            if (x.type === snippetTypes.HEADER) {
                              return (
                                <div className={className}>{setting.text}</div>
                              )
                            }

                            return (
                              <div className={className}>
                                {idx++}. {setting.text}: {setting.value}
                              </div>
                            )
                          })()}
                        </div>
                      )),
                      filter(
                        ({ setting, ...x }: SnippetProps) =>
                          (!isNil(setting) && !isNil(setting.value)) ||
                          x.type === snippetTypes.HEADER ||
                          x.inline
                      ),
                      pathOr([], [key])
                    )(_snippetList)}
                  </div>
                )),
                keys
              )(_snippetList)}
            </SnippetPreviewWrapper>
          </Col>
        </Row>
        <Divider />
        <Row gutter={16}>
          <Col span={8}>
            <FormItem {...formItemLayout} label="判讀醫師">
              <Input readOnly bordered={false} value={user.name} />
            </FormItem>
          </Col>
          <Col span={8}>
            <FormItem {...formItemLayout} label="複審醫師">
              <Input readOnly bordered={false} value={user.name} />
            </FormItem>
          </Col>
          <Col span={8}>
            <FormItem {...formItemLayout} label="申請醫師">
              <Input readOnly bordered={false} value={user.name} />
            </FormItem>
          </Col>
        </Row>
        <Row gutter={16}>
          <Col span={12}>
            <FormItem {...formItemLayout} label="報告輸入人員">
              <Input bordered={false} />
            </FormItem>
          </Col>
          <Col span={12}>
            <FormItem {...formItemLayout} label="報告複審人員">
              <Input bordered={false} />
            </FormItem>
          </Col>
        </Row>
        <Divider />
        <Row className="mb-4">
          <Col span={24}>
            <div className="text-center">病解專醫字第000001號</div>
          </Col>
        </Row>

        <Row className="mt-4">
          <Col span={24}>
            <SnippetPreviewWrapper>
              {compose(
                map((key: string) => (
                  <div
                    key={key}
                    css={{
                      fontSize: 15,
                      lineHeight: 1.65,
                      color: 'rgba(0, 0, 0, 0.85)',
                    }}>
                    {compose(
                      gt(__, 0),
                      length,
                      pathOr([], [key])
                    )(snippetList) && (
                      <h2
                        className="my-1 mt-5 font-normal underline"
                        css={{ fontSize: 16 }}>
                        {key}
                      </h2>
                    )}
                    {compose(
                      mapIndexed(({ setting, ...x }: SnippetProps, _idx: number) => (
                        <span key={x.id}>
                          {(() => {
                            let className = `ml-0`

                            let textOnly = false

                            if (key === 'Pathological diagnosis') {
                              textOnly = true
                            }

                            if (x.inline) {
                              return (
                                <span
                                  className={className}
                                  dangerouslySetInnerHTML={{
                                    __html: getHtmlText(
                                      x as any,
                                      textOnly ? undefined : _idx + 1
                                    ),
                                  }}
                                />
                              )
                            }

                            if (x.type === snippetTypes.HEADER) {
                              return textOnly ? (
                                `${setting.text}.`
                              ) : (
                                <div className={className}>{_idx + 1}. {setting.text}</div>
                              )
                            }

                            const value = ifElse(
                              hasPath(['options']),
                              compose(
                                join(', '),
                                map((x: ReportTemplateSettingOption) => x.text),
                                pathOr([], ['options'])
                              ),
                              always(setting.value)
                            )(setting)

                            return textOnly ? (
                              `${setting.text}, ${value}.`
                            ) : (
                              <div className={className}>
                                {_idx + 1}. {setting.text}: {setting.value}
                              </div>
                            )
                          })()}
                        </span>
                      )),
                      filter(
                        ({ setting, ...x }: SnippetProps) =>
                          (!isNil(setting) && !isNil(setting.value)) ||
                          x.type === snippetTypes.HEADER ||
                          x.inline
                      ),
                      pathOr([], [key])
                    )(_snippetList)}
                  </div>
                )),
                keys
              )(_snippetList)}
            </SnippetPreviewWrapper>
          </Col>
        </Row>
      </>
    )
  }

  return (
    <>
      <div style={style} className={cx('js-export-pdf', className)}>
        {renderReport()}
      </div>
    </>
  )
}
